export default{
	components:{
		
	},
	data(){
		return{
			scrollH:500,
			// 当前选中的分类
			activeIndex:0,
			cate:[],
			list:[],
			leftDomsTop:[],
			rightDomsTop:[]
		}
	},
	onLoad() {
		// 获取可视区域高度
		uni.getSystemInfo({
			success: res => {
				// 转换单位 px->upx
				this.scrollH = res.windowHeight - uni.upx2px(80);
			}
		})
		this.getData()
	},
	onReady() {
		const query = uni.createSelectorQuery().in(this);
		let left = []
		let right = []
		query.selectAll('.left-scroll-item').boundingClientRect(data => {
		  // console.log("得到布局位置信息" + JSON.stringify(data));
		  // console.log("节点离页面顶部的距离为" + data.top);
		  left = data.map(v=>{
			  return v.top
		  })
		  this.leftDomsTop = left
		}).exec();
		query.selectAll('.right-scroll-item').boundingClientRect(data => {
		  // console.log("得到布局位置信息" + JSON.stringify(data));
		  // console.log("节点离页面顶部的距离为" + data.top);
		  right = data.map(v=>{
			  return v.top
		  })
		  this.rightDomsTop = right
		}).exec();
	},
	methods:{
		search(){
			uni.navigateTo({
			    url: '/pages/search-list/search-list'
			});
		},
		getData(){
			for ( let i= 0;i<20;i++){
				this.cate.push({
					name:"分类"+ i
				})
				this.list.push({
					list:[]
				})
			}
			for (let i = 0; i < this.list.length; i++) {
				for (let j = 0; j < 21; j++) {
					this.list[i].list.push({
						src:'../../static/images/demo/cate_01.png',
						name:`分类${i}-商品${j}`,
					})
				}
			}
		},
		changeCate(index){
			this.activeIndex = index
		}
	}
}
